import React, { useState, useEffect, useRef } from 'react';

class AgenticRAGClient {
    constructor(baseUrl = 'http://127.0.0.1:5000') {
        this.baseUrl = baseUrl;
        this.currentTemplateId = null;
        this.agentReasoning = null;
    }

    async generateUI(command) {
        try {
            const response = await fetch(`${this.baseUrl}/generate-ui`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ command }),
            });
            const data = await response.json();
            if (response.ok) {
                this.currentTemplateId = data.template_id;
                this.agentReasoning = data.agent_reasoning;
                return {
                    success: true,
                    html: data.html,
                    templateId: data.template_id,
                    source: data.source,
                    reasoning: data.agent_reasoning,
                };
            } else {
                return {
                    success: false,
                    error: data.error,
                    agentResponse: data.agent_response,
                };
            }
        } catch (error) {
            return { success: false, error: `Network error: ${error.message}` };
        }
    }

    async searchTemplates(query, maxResults = 5) {
        try {
            const response = await fetch(`${this.baseUrl}/search-templates`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ query, max_results: maxResults }),
            });
            const data = await response.json();
            if (response.ok) {
                return {
                    success: true,
                    results: data.results,
                    totalFound: data.total_found,
                    query: data.query,
                };
            } else {
                return { success: false, error: data.error };
            }
        } catch (error) {
            return { success: false, error: `Network error: ${error.message}` };
        }
    }

    async saveUI(html, parentTemplateId = null, user = 'test_user') {
        try {
            const response = await fetch(`${this.baseUrl}/save-ui`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    html,
                    parent_template_id: parentTemplateId || this.currentTemplateId,
                    user,
                }),
            });
            const data = await response.json();
            if (response.ok) {
                this.currentTemplateId = data.template_id;
                return {
                    success: true,
                    message: data.message,
                    templateId: data.template_id,
                };
            } else {
                return { success: false, error: data.error };
            }
        } catch (error) {
            return { success: false, error: `Network error: ${error.message}` };
        }
    }
}

const ragClient = new AgenticRAGClient();

const App = () => {
    const [command, setCommand] = useState('');
    const [searchQuery, setSearchQuery] = useState('');
    const [status, setStatus] = useState({ message: '', type: '' });
    const [loading, setLoading] = useState(false);
    const [uiContent, setUiContent] = useState({ html: '<p className="text-center text-gray-500 italic mt-24">Generated UI will appear here...</p>', templateId: '', source: '' });
    const [searchResults, setSearchResults] = useState([]);
    const [totalFound, setTotalFound] = useState(0);
    const [reasoning, setReasoning] = useState({ content: '', source: '' });
    const [modalContent, setModalContent] = useState(null);
    const uiContainerRef = useRef(null);

    const showStatus = (message, type) => {
        setStatus({ message, type });
        if (type !== 'error') {
            setTimeout(() => setStatus({ message: '', type: '' }), 5000);
        }
    };

    const showLoading = (show) => setLoading(show);

    const generateUI = async () => {
        if (!command.trim()) {
            showStatus('Please enter a command to generate UI.', 'error');
            return;
        }
        showStatus('ü§ñ Agent is analyzing your request...', 'info');
        showLoading(true);
        try {
            const result = await ragClient.generateUI(command);
            if (result.success) {
                setUiContent({ html: result.html, templateId: result.templateId, source: result.source });
                setReasoning({ content: result.reasoning, source: result.source });
                showStatus(`‚úÖ UI generated successfully using ${result.source}!`, 'success');
            } else {
                showStatus(`‚ùå Error: ${result.error}`, 'error');
                if (result.agentResponse) {
                    setReasoning({ content: result.agentResponse, source: 'error' });
                }
            }
        } catch (error) {
            showStatus(`‚ùå Unexpected error: ${error.message}`, 'error');
        } finally {
            showLoading(false);
        }
    };

    const searchTemplates = async () => {
        if (!searchQuery.trim()) {
            showStatus('Please enter a search query.', 'error');
            return;
        }
        showStatus('üîç Searching for similar templates...', 'info');
        showLoading(true);
        try {
            const result = await ragClient.searchTemplates(searchQuery, 5);
            if (result.success) {
                setSearchResults(result.results);
                setTotalFound(result.totalFound);
                showStatus(`üîç Found ${result.totalFound} similar templates`, 'success');
            } else {
                showStatus(`‚ùå Search error: ${result.error}`, 'error');
            }
        } catch (error) {
            showStatus(`‚ùå Search failed: ${error.message}`, 'error');
        } finally {
            showLoading(false);
        }
    };

    const saveUI = async () => {
        if (!uiContainerRef.current || uiContainerRef.current.innerHTML.includes('Generated UI will appear here')) {
            showStatus('No UI to save. Generate one first.', 'error');
            return;
        }
        showStatus('üíæ Saving UI template...', 'info');
        showLoading(true);
        try {
            const result = await ragClient.saveUI(uiContainerRef.current.innerHTML);
            if (result.success) {
                showStatus(`‚úÖ ${result.message} (ID: ${result.templateId})`, 'success');
            } else {
                showStatus(`‚ùå Save error: ${result.error}`, 'error');
            }
        } catch (error) {
            showStatus(`‚ùå Save failed: ${error.message}`, 'error');
        } finally {
            showLoading(false);
        }
    };

    const clearUI = () => {
        setUiContent({ html: '<p className="text-center text-gray-500 italic mt-24">Generated UI will appear here...</p>', templateId: '', source: '' });
        setReasoning({ content: '', source: '' });
        showStatus('üßπ UI cleared', 'success');
    };

    const clearSearch = () => {
        setSearchResults([]);
        setSearchQuery('');
        setTotalFound(0);
        showStatus('üßπ Search results cleared', 'success');
    };

    const useTemplate = (templateId) => {
        showStatus(`üìã Loading template ${templateId}...`, 'info');
    };

    const viewTemplate = (templateId) => {
        const template = searchResults.find(t => t.template_id === templateId);
        if (template) {
            setModalContent({ id: templateId, html: template.html_preview });
        }
    };

    const copyToClipboard = (text) => {
        navigator.clipboard.writeText(text).then(() => {
            showStatus('üìã HTML copied to clipboard!', 'success');
        });
    };

    const handleExampleClick = (prompt) => {
        setCommand(prompt);
        generateUI();
    };

    useEffect(() => {
        const handleKeyDown = (e) => {
            if (e.ctrlKey || e.metaKey) {
                e.preventDefault();
                switch (e.key) {
                    case 'Enter':
                        generateUI();
                        break;
                    case 's':
                        saveUI();
                        break;
                    case 'f':
                        document.getElementById('searchInput').focus();
                        break;
                }
            }
        };
        document.addEventListener('keydown', handleKeyDown);
        return () => document.removeEventListener('keydown', handleKeyDown);
    }, [command, uiContent]);

    useEffect(() => {
        const observer = new MutationObserver(() => {
            clearTimeout(window.autoSaveTimeout);
            window.autoSaveTimeout = setTimeout(() => {
                if (uiContainerRef.current && !uiContainerRef.current.innerHTML.includes('Generated UI will appear here')) {
                    showStatus('üíæ Auto-saving...', 'info');
                    saveUI();
                }
            }, 5000);
        });
        if (uiContainerRef.current) {
            observer.observe(uiContainerRef.current, { childList: true, subtree: true });
        }
        return () => observer.disconnect();
    }, []);

    return (
        <div className="min-h-screen bg-gradient-to-br from-indigo-500 to-purple-600 font-sans text-gray-800">
            <div className="max-w-7xl mx-auto p-5">
                <header className="text-center text-white mb-8">
                    <h1 className="text-4xl font-bold mb-2 text-shadow">ü§ñ Agentic RAG UI Generator</h1>
                    <p className="text-lg opacity-90">Intelligent UI template generation with Retrieval Augmented Generation</p>
                </header>

                <div className={status.message ? `p-4 rounded-lg mb-4 ${status.type === 'success' ? 'bg-green-100 text-green-800 border border-green-300' : status.type === 'error' ? 'bg-red-100 text-red-800 border border-red-300' : 'bg-blue-100 text-blue-800 border border-blue-300'}` : ''}>
                    {status.message && <div>{status.message}</div>}
                </div>

                {loading && (
                    <div className="text-center p-5">
                        <div className="border-4 border-gray-200 border-t-indigo-500 rounded-full w-10 h-10 animate-spin mx-auto mb-2"></div>
                        <p>Agent is processing your request...</p>
                    </div>
                )}

                <div className="grid grid-cols-1 md:grid-cols-2 gap-5 mb-5">
                    <div className="bg-white rounded-xl p-6 shadow-lg backdrop-blur">
                        <h2 className="text-xl text-gray-700 mb-5 border-b-2 border-gray-200 pb-2">üé® Generate UI</h2>
                        <div className="mb-5">
                            <label htmlFor="commandInput" className="block font-semibold text-gray-700 mb-2">Describe the UI you want to create:</label>
                            <input
                                id="commandInput"
                                type="text"
                                value={command}
                                onChange={(e) => setCommand(e.target.value)}
                                onKeyPress={(e) => e.key === 'Enter' && generateUI()}
                                placeholder="e.g., Create a modern login form with email and password"
                                className="w-full p-3 border-2 border-gray-200 rounded-lg focus:outline-none focus:border-indigo-500 focus:ring-2 focus:ring-indigo-100"
                            />
                        </div>
                        <div className="flex flex-wrap gap-2">
                            <button onClick={generateUI} className="bg-gradient-to-r from-indigo-500 to-purple-600 text-white px-6 py-3 rounded-lg font-semibold hover:shadow-lg hover:-translate-y-0.5 transition">üöÄ Generate UI</button>
                            <button onClick={saveUI} className="bg-gradient-to-r from-green-500 to-green-600 text-white px-6 py-3 rounded-lg font-semibold hover:shadow-lg hover:-translate-y-0.5 transition">üíæ Save UI</button>
                            <button onClick={clearUI} className="bg-gray-100 text-gray-700 border-2 border-gray-200 px-6 py-3 rounded-lg font-semibold hover:shadow-lg hover:-translate-y-0.5 transition">üßπ Clear</button>
                        </div>
                        <div className="bg-green-50 border border-green-200 rounded-lg p-4 mt-5">
                            <h4 className="text-green-800 mb-2">üí° Example Prompts:</h4>
                            <ul className="list-none">
                                {['Create a modern login form with email and password', 'Build an innovative dashboard with dark theme', 'Make a contact form with validation', 'Design a product card with image and price', 'Create a navigation menu with dropdown'].map((prompt) => (
                                    <li key={prompt} onClick={() => handleExampleClick(prompt)} className="text-green-600 cursor-pointer hover:underline py-1">{prompt}</li>
                                ))}
                            </ul>
                        </div>
                    </div>

                    <div className="bg-white rounded-xl p-6 shadow-lg backdrop-blur">
                        <h2 className="text-xl text-gray-700 mb-5 border-b-2 border-gray-200 pb-2">üîç Search Templates</h2>
                        <div className="mb-5">
                            <label htmlFor="searchInput" className="block font-semibold text-gray-700 mb-2">Search for existing templates:</label>
                            <input
                                id="searchInput"
                                type="text"
                                value={searchQuery}
                                onChange={(e) => setSearchQuery(e.target.value)}
                                onKeyPress={(e) => e.key === 'Enter' && searchTemplates()}
                                placeholder="e.g., login form, dashboard, contact form"
                                className="w-full p-3 border-2 border-gray-200 rounded-lg focus:outline-none focus:border-indigo-500 focus:ring-2 focus:ring-indigo-100"
                            />
                        </div>
                        <div className="flex flex-wrap gap-2">
                            <button onClick={searchTemplates} className="bg-gradient-to-r from-indigo-500 to-purple-600 text-white px-6 py-3 rounded-lg font-semibold hover:shadow-lg hover:-translate-y-0.5 transition">üîç Search</button>
                            <button onClick={clearSearch} className="bg-gray-100 text-gray-700 border-2 border-gray-200 px-6 py-3 rounded-lg font-semibold hover:shadow-lg hover:-translate-y-0.5 transition">üßπ Clear Results</button>
                        </div>
                        <div className="mt-5">
                            {searchResults.length === 0 ? (
                                totalFound === 0 && <p className="text-gray-500">No similar templates found.</p>
                            ) : (
                                <>
                                    <h3 className="text-lg text-gray-800 mb-4">Search Results for "{searchQuery}" ({totalFound} found)</h3>
                                    <div className="grid gap-4">
                                        {searchResults.map((template, index) => {
                                            const scoreColor = template.similarity_score < 0.2 ? 'bg-green-100 text-green-800' : template.similarity_score < 0.4 ? 'bg-yellow-100 text-yellow-800' : 'bg-red-100 text-red-800';
                                            return (
                                                <div key={index} className="border border-gray-200 rounded-lg p-4 bg-gray-50">
                                                    <div className="flex justify-between items-center mb-2">
                                                        <h4 className="text-gray-800">{template.component} - {template.purpose}</h4>
                                                        <span className={`px-2 py-1 rounded text-xs font-bold ${scoreColor}`}>{(1 - template.similarity_score).toFixed(3)} match</span>
                                                    </div>
                                                    <div className="text-sm text-gray-600 mb-2">
                                                        <p><strong>Style:</strong> {template.style}</p>
                                                        <p><strong>Fields:</strong> {template.fields.join(', ') || 'None'}</p>
                                                        <p><strong>Source:</strong> {template.source}</p>
                                                    </div>
                                                    <div className="bg-gray-800 text-gray-200 p-2 rounded font-mono text-xs overflow-x-auto mb-2">{template.html_preview}</div>
                                                    <div className="flex gap-2">
                                                        <button onClick={() => useTemplate(template.template_id)} className="bg-indigo-500 text-white px-3 py-1 rounded text-sm hover:shadow-lg hover:-translate-y-0.5 transition">Use This Template</button>
                                                        <button onClick={() => viewTemplate(template.template_id)} className="bg-gray-200 text-gray-700 px-3 py-1 rounded text-sm hover:shadow-lg hover:-translate-y-0.5 transition">View Full HTML</button>
                                                    </div>
                                                </div>
                                            );
                                        })}
                                    </div>
                                </>
                            )}
                        </div>
                    </div>
                </div>

                {reasoning.content && (
                    <div className="bg-gray-50 border border-gray-200 rounded-xl p-6 mb-5">
                        <h3 className="text-lg text-gray-800 mb-4 flex items-center gap-2">
                            {reasoning.source === 'vector_db' && 'üîç'}
                            {reasoning.source === 'mongo_query' && 'üóÉÔ∏è'}
                            {reasoning.source === 'llm_generation_with_context' && 'üß†üí°'}
                            {reasoning.source === 'llm_generation_from_scratch' && '‚ú®'}
                            {reasoning.source === 'error' && '‚ùå'}
                            Agent Reasoning
                        </h3>
                        <div className="bg-white p-4 rounded border-l-4 border-indigo-500">
                            <p><strong>Source:</strong> {reasoning.source}</p>
                            <div className="font-mono text-sm text-gray-800 whitespace-pre-wrap mt-2">{reasoning.content}</div>
                        </div>
                    </div>
                )}

                <div className="bg-white rounded-xl p-6 shadow-lg backdrop-blur">
                    <h2 className="text-xl text-gray-700 mb-5 border-b-2 border-gray-200 pb-2">üéØ Generated UI</h2>
                    <div ref={uiContainerRef} className="bg-gray-50 border-2 border-dashed border-gray-300 rounded-xl p-8 min-h-[300px] overflow-auto" dangerouslySetInnerHTML={{ __html: uiContent.html }}></div>
                </div>

                <div className="bg-blue-50 border border-blue-200 rounded-xl p-4 mt-5">
                    <h4 className="text-blue-800 mb-2">‚å®Ô∏è Keyboard Shortcuts</h4>
                    <div className="flex justify-between mb-2">
                        <span>Generate UI</span>
                        <span><span className="bg-gray-800 text-white px-1.5 py-0.5 rounded text-xs font-mono">Ctrl</span> + <span className="bg-gray-800 text-white px-1.5 py-0.5 rounded:oq text-xs font-mono">Enter</span></span>
                    </div>
                    <div className="flex justify-between mb-2">
                        <span>Save UI</span>
                        <span><span className="bg-gray-800 text-white px-1.5 py-0.5 rounded text-xs font-mono">Ctrl</span> + <span className="bg-gray-800 text-white px-1.5 py-0.5 rounded text-xs font-mono">S</span></span>
                    </div>
                    <div className="flex justify-between">
                        <span>Focus Search</span>
                        <span><span className="bg-gray-800 text-white px-1.5 py-0.5 rounded text-xs font-mono">Ctrl</span> + <span className="bg-gray-800 text-white px-1.5 py-0.5 rounded text-xs font-mono">F</span></span>
                    </div>
                </div>

                <div className="bg-white rounded-xl p-6 mt-5 shadow-lg backdrop-blur">
                    <h2 className="text-xl text-gray-700 mb-5 border-b-2 border-gray-200 pb-2">üß† How Agentic RAG Works</h2>
                    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-5">
                        <div className="bg-green-50 p-4 rounded-lg">
                            <h4 className="text-green-800 mb-2">üîç Search Phase</h4>
                            <p className="text-green-600 text-sm">Agent searches for similar templates using semantic similarity in the vector database.</p>
                        </div>
                        <div className="bg-blue-50 p-4 rounded-lg">
                            <h4 className="text-blue-800 mb-2">ü§î Decision Phase</h4>
                            <p className="text-blue-600 text-sm">Agent evaluates search results and decides on the best generation strategy.</p>
                        </div>
                        <div className="bg-yellow-50 p-4 rounded-lg">
                            <h4 className="text-yellow-800 mb-2">üé® Generation Phase</h4>
                            <p className="text-yellow-600 text-sm">Agent generates UI using context from retrieved templates or creates from scratch.</p>
                        </div>
                    </div>
                </div>

                {modalContent && (
                    <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center z-50">
                        <div className="bg-white rounded-xl max-w-4xl max-h-[80vh] overflow-auto">
                            <div className="p-5 border-b border-gray-200 flex justify-between items-center">
                                <h3 className="text-lg text-gray-800">Template {modalContent.id}</h3>
                                <button onClick={() => setModalContent(null)} className="text-gray-600 text-2xl">√ó</button>
                            </div>
                            <div className="p-5">
                                <pre className="font-mono text-sm bg-gray-800 text-gray-200 p-4 rounded">{modalContent.html}</pre>
                            </div>
                            <div className="p-5 border-t border-gray-200 flex justify-end gap-2">
                                <button onClick={() => copyToClipboard(modalContent.html)} className="bg-indigo-500 text-white px-4 py-2 rounded hover:shadow-lg hover:-translate-y-0.5 transition">Copy HTML</button>
                                <button onClick={() => setModalContent(null)} className="bg-gray-200 text-gray-700 px-4 py-2 rounded hover:shadow-lg hover:-translate-y-0.5 transition">Close</button>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
};

export default App;